import React,{ useRef, useEffect } from 'react'
import "./Header.css"
import gsap from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger';

gsap.registerPlugin(ScrollTrigger);

const Header = () => {
    const wrapPhoto = useRef(null);
    const wrap1 = useRef(null);
    const wrap2 = useRef(null);
  
    useEffect(() => {
        if ( wrapPhoto.current && wrap1.current) {

          if( window.innerWidth > 1024 ){
            gsap.fromTo(wrapPhoto.current, 
                {
                  y: 0,
                  x: 0,
                  scale: 1.1,
                },
                {
                  y: 600,
                  x: 100,
                  scale: 1,
                  duration: 8,
                  scrollTrigger: {
                      trigger: wrapPhoto.current,
                      start: 'top 40%',
                      end: 'top -30%',
                      scrub: 4,
                      toggleActions: 'restart none none none',
                  }
                }
              ); 
            }

            gsap.to(wrap1.current, {
                transform: "rotate(-5deg)",
                bottom: "-20%",
                scrollTrigger: {
                  trigger: wrap1.current,
                  start: "top 95%", // Atur trigger point saat elemen tampil di tengah halaman
                  end: "top 70%", // Atur end point saat elemen tampil di tengah halaman
                  scrub: 1
                },
              });

              gsap.to(wrap2.current, {
                transform: "rotate(10deg)",
                bottom: "-22%",
                scrollTrigger: {
                  trigger: wrap1.current,
                  start: "top 95%", // Atur trigger point saat elemen tampil di tengah halaman
                  end: "top 70%", // Atur end point saat elemen tampil di tengah halaman
                  scrub: 1
                },
              });
        }
    },[]);

    return (
        <div className='header position-relative d-flex flex-wrap align-content-center'>

            <div className='wrap-title'>
                <h1 className='title-header'>An enthusiastic</h1>
                <h1 className='title-header'>website developer</h1>
                <h1 className='title-header'>from Indonesia.</h1>
            </div>

            <img ref={wrapPhoto} src="https://assets.awwwards.com/awards/media/cache/thumb_880_660/element/2023/04/643eaa8a54d2b756082987.png" alt='Coding' className='wrap-photo-header position-relative ms-auto rounded-4'>
            </img>

            <div ref={wrap1} className='wrap-1 position-absolute'></div>
            <div ref={wrap2} className='wrap-2 position-absolute'></div>

        </div>  
    )
}

export default Header